    font-size: 50px;
    transition: .5s;
}

.set_radios {
    margin: 10px;
}

.set_radios p {
    font-size: 25px;
    font-weight: 700;
    color: var(--text-color);
}

#add-to-box {
    width: 30%;
    min-width: 200px;
}

#lastrecite {
    max-height: 150px;
}

#range {
    max-height: 85%;
}

#rangeselect {
    max-height: 100%;
    overflow-x: auto;
}
</style>